*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  background: #fff;
  font-family: 'Helvetica Neue', Helvetica, 'Microsoft YaHei', FreeSans, Arimo,
    'Droid Sans', 'wenquanyi micro hei', 'Hiragino Sans GB',
    'Hiragino Sans GB W3', Arial, sans-serif;
  font-weight: normal;
  color: #555;
  font-size: 14px;
}

h2,
h3,
h4 {
  font-weight: normal;
}

input[type='text'],
input[type='email'],
input[type='tel'] {
  border: 1px solid #a7a7a7;
  height: 46px;
  padding: 0 20px;
  color: #a7a7a7;
  display: block;
  width: 100%;
  outline: none;
  border-radius: 0;
  box-shadow: none;
}

input[type='text']:focus,
input[type='email']:focus,
input[type='tel']:focus {
  border-color: #999;
}

.btn {
  display: block;
  width: 100%;
  height: 50px;
  border-radius: 3px;
  border: 1px solid #1d1d1f;
  text-align: center;
  text-decoration: none;
  outline: none;
  background-color: #1d1d1f;
  color: #fff;
}

.btn[disabled] {
  border-color: #aaa;
  color: #aaa;
}

a.btn {
  line-height: 48px;
}

.App-header {
  display: flex;
  align-items: center;
  background-color: #222;
  height: 100px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 60px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.bottom-action {
  position: fixed;
  bottom: 10px;
  left: 10px;
  right: 10px;
}

.popup .popup-content {
  position: fixed;
  background-color: #fff;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 10px;
  z-index: 10;
  -webkit-animation: fadeUp 0.4s ease both;
          animation: fadeUp 0.4s ease both;
}

.popup .popup-content input {
  margin: 10px 0;
}

.backdrop {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.6);
}

.btn {
  display: block;
  width: 100%;
  height: 50px;
  border-radius: 3px;
  border: 1px solid #1d1d1f;
  text-align: center;
  text-decoration: none;
  outline: none;
  background-color: #1d1d1f;
  color: #fff;
}

.btn[disabled] {
  border-color: #aaa;
  color: #aaa;
}

a.btn {
  line-height: 48px;
}

.price {
  padding: 5px 10px;
  background-color: #000;
  text-align: right;
  color: #fff;
}

.list {
  margin: 10px 10px 50px;
}

.header {
  margin: 10px;
  display: inline-flex;
  align-items: center;
}
.header-logo {
  display: inline-block;
  height: 30px;
}

.header-title {
  font-weight: bold;
}

.actions button {
  background: #000;
  color: #fff;
  border: 0 none;
  padding: 3px 5px;
  vertical-align: middle;
}

.product {
  border-bottom: 1px dashed #000;
  margin-bottom: 5px;
  padding: 5px;
  line-height: 1.6;
  display: flex;
  font-size: 12px;
}

.product > .code {
  flex: 1 1;
}

